/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { createBlock, registerBlockType } from '@wordpress/blocks';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
// import { PanelBody, BaseControl, ToggleControl, SelectControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import icon from './icon';
import deprecated from './deprecated';
import TheControls from './controls';
import { LsSelectorPreviewTip } from '@components';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

const getSpaceClass = ({ padding, paddingTAB, paddingMB, margin, marginTAB, marginMB }) => {
	return classnames({
		'arkb-has-padding': !!padding || !!paddingTAB || !!paddingMB,
		'arkb-has-margin': !!margin || !!marginTAB || !!marginMB,
	});
};

/**
 * コンテナ
 */
const blockName = 'ark-block-container';
registerBlockType(metadata.name, {
	title: __('Container', 'arkhe-blocks'),
	icon: icon.container,
	// variations,
	transforms: {
		from: [
			//どのブロックタイプから変更できるようにするか
			{
				type: 'block',
				blocks: ['core/group'],
				transform: (attributes, content) => {
					// console.log('attributes', attributes);
					const newInnerBlocks = content;
					const newAttrs = {
						tagName: attributes.tagName,
					};
					return createBlock(metadata.name, newAttrs, newInnerBlocks);
				},
			},
		],
	},
	edit: ({ clientId, attributes, setAttributes }) => {
		const {
			layout = {},
			templateLock,
			tagName: TagName = 'div',
			// gap,
			padding,
			paddingTAB,
			paddingMB,
			margin,
			marginTAB,
			marginMB,
			hasInner,
			hasInnerSpace,
		} = attributes;

		// バージョンを更新
		useEffect(() => {
			if ('2' !== attributes.version) {
				setAttributes({ version: '2' });
			}
		}, []);

		const isFlex = layout?.type === 'flex';
		// const isHorizontal = orientation === 'horizontal';

		const spaceClass = getSpaceClass({
			padding,
			paddingTAB,
			paddingMB,
			margin,
			marginTAB,
			marginMB,
		});

		if (hasInner) {
			let parentClass = classnames(blockName, 'has-inner', 'ark-has-guide');
			let innerClass = classnames(`${blockName}__inner`, {
				'ark-keep-mt': !isFlex,
			});

			// padding, marginをどっちに適用するか
			if (hasInnerSpace) {
				innerClass = classnames(innerClass, spaceClass);
			} else {
				parentClass = classnames(parentClass, spaceClass);
			}

			const blockProps = useBlockProps({
				className: parentClass,
				'data-flex': layout?.orientation || null,
			});
			const innerBlocksProps = useInnerBlocksProps(
				{
					className: innerClass,
				},
				{
					template: [['core/paragraph']],
					templateLock,
					__experimentalLayout: layout,
				}
			);

			return (
				<>
					<TheControls {...{ clientId, attributes, setAttributes }} />
					<TagName {...blockProps}>
						<LsSelectorPreviewTip
							icon={icon.container}
							anchor={attributes.anchor}
							className={attributes.className}
						/>
						<div {...innerBlocksProps} />
					</TagName>
				</>
			);
		}

		const blockProps = useBlockProps({
			className: classnames(blockName, 'ark-has-guide', spaceClass, {
				'ark-keep-mt': !isFlex,
			}),
			'data-flex': layout?.orientation || null,
		});

		const innerBlocksProps = useInnerBlocksProps(blockProps, {
			template: [['core/paragraph']],
			templateLock,
			__experimentalLayout: layout,
		});

		const { children, ...innerProps } = innerBlocksProps;

		return (
			<>
				<TheControls {...{ clientId, attributes, setAttributes }} />
				<TagName {...innerProps}>
					<LsSelectorPreviewTip
						icon={icon.container}
						anchor={attributes.anchor}
						className={attributes.className}
					/>
					{children}
				</TagName>
			</>
		);
	},

	save: ({ attributes }) => {
		const {
			layout,
			hasInner,
			hasInnerSpace,
			padding,
			paddingTAB,
			paddingMB,
			margin,
			marginTAB,
			marginMB,
			tagName: TagName = 'div',
		} = attributes;

		const isFlex = layout?.type === 'flex';

		const spaceClass = getSpaceClass({
			padding,
			paddingTAB,
			paddingMB,
			margin,
			marginTAB,
			marginMB,
		});

		if (hasInner) {
			let parentClass = classnames(blockName, 'has-inner');
			let innerClass = classnames(`${blockName}__inner`, {
				'ark-keep-mt': !isFlex,
			});

			// padding, marginをどっちに適用するか
			if (hasInnerSpace) {
				innerClass = classnames(innerClass, spaceClass);
			} else {
				parentClass = classnames(parentClass, spaceClass);
			}

			const blockProps = useBlockProps.save({
				className: parentClass,
				'data-flex': layout?.orientation || null,
			});
			return (
				<TagName {...blockProps}>
					<div className={innerClass}>
						<InnerBlocks.Content />
					</div>
				</TagName>
			);
		}

		// 以下、innerなし
		const blockProps = useBlockProps.save({
			className: classnames(blockName, spaceClass, {
				'ark-keep-mt': !isFlex,
			}),
			'data-flex': layout?.orientation || null,
		});

		return (
			<TagName {...blockProps}>
				<InnerBlocks.Content />
			</TagName>
		);
	},
	deprecated,
});
