/**
 * @WordPress dependencies
 */
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

/**
 * @Others dependencies
 */
import classnames from 'classnames';

/**
 * 変数
 */
const blockName = 'ark-block-container';

export default [
	// 2.21 より前。margin設定追加する前で、has-paddingクラスがなかった。
	{
		supports: {
			anchor: true,
			className: false,
			align: ['wide', 'full'],
			color: {
				gradients: true,
				__experimentalDefaultControls: {
					background: true,
					text: true,
				},
			},
		},
		attributes: {
			layout: {
				type: 'object',
			},
			templateLock: {
				type: ['string', 'boolean'],
				enum: ['all', 'insert', false],
			},
			tagName: {
				type: 'string',
				default: 'div',
			},
			hasInner: {
				type: 'boolean',
				default: false,
			},
			gap: {
				type: 'object',
			},
			padding: {
				type: 'object',
			},
		},
		migrate: (attributes) => {
			const newAttrs = { ...attributes };
			newAttrs.hasInnerSpace = true;
			newAttrs.version = '2';
			return newAttrs;
		},
		save: ({ attributes }) => {
			const { layout, hasInner, tagName: TagName = 'div' } = attributes;
			const isFlex = layout?.type === 'flex';

			const blockProps = useBlockProps.save({
				className: classnames(blockName, {
					'has-inner': hasInner,
					'ark-keep-mt': !hasInner && !isFlex,
				}),
				'data-flex': layout?.orientation || null,
			});

			return (
				<TagName {...blockProps}>
					{!hasInner && <InnerBlocks.Content />}
					{hasInner && (
						<div
							className={classnames(`${blockName}__inner`, {
								'ark-keep-mt': !isFlex,
							})}
						>
							<InnerBlocks.Content />
						</div>
					)}
				</TagName>
			);
		},
	},
];
